import { ImgAbout, MainScreen, TextAbout, TextInfoMe } from "./styles";
import imgPerfil from "../../assets/fotoPerfil.png";

const AboutPage = () => {
  return (
    <>
      <MainScreen>
        <TextAbout>SOBRE: </TextAbout>
        <ImgAbout src={imgPerfil}></ImgAbout>
        <TextInfoMe>
          Sou uma pessoa dinmica, curiosa e apaixonada por aprender. Tenho
          facilidade em me adaptar a desafios e gosto de colaborar em solues
          inovadoras. Valorizo o trabalho em equipe e busco crescer
          profissionalmente.
        </TextInfoMe>
        <TextInfoMe>
          Participo de um projeto de deteco de melanomas por inteligncia
          artificial, no qual desenvolvi o site de apresentao. O trabalho foi
          premiado em vrios estados do Brasil e nos credenciou para feiras
          cientficas em Nova Iorque e na Itlia.
        </TextInfoMe>
      </MainScreen>
    </>
  );
};

export { AboutPage };
